@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600&display=swap);
.about_container {
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
  }
  
  .about_me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary),transparent);
    display: grid;
    place-items: center;
  }
  
  .about_me-image {
    border-radius: 2rem;
    overflow: hidden;
  }
  
  .about_cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
  }
  
  .about_card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
  }
  
  .about_card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
  }
  
  .about_icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
  }
  
  .about_card h5 {
    font-size: 0.9rem;
  }
  
  .about_card small {
    font-size: 0.7rem;
    color: var(--color-light);
  }
  
  .about_content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-white); 
  }
  
  @media screen and (max-width: 1024px){
    .about_container {
      grid-template-columns: 1fr;
      grid-gap: 0;
      gap: 0;
    }
  
    .about_me {
      width: 50%;
      margin: 2rem auto 4rem;
    }
  
    .about_content p {
      margin: 1rem 0 1.5rem;
    }
  }
  
  /*===== Media Query(Mobile)===*/
  @media screen and (max-width: 600px){
    .about_me {
      width: 65%;
      margin: 0 auto 3rem;
    }
  
    .about_cards {
      grid-template-columns: 1fr 1fr;
      grid-gap: 1rem;
      gap: 1rem;
    }
  
    .about_content {
      text-align: center;
    }
  
    .about_content p {
      margin: 1.5rem 0;
    }
  
  }
.container.contact_container {
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
  }
  
  .contact_options {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
  }
  
  .contact_option {
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
  }
  
  .contact_option:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
  }
  
  .contact_option-icon {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
  }
  
  .contact_option a {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
  }
  
  form {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
  }
  
  input,
  textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white);
  }
  
  @media screen and (max-width: 1024px){
   .container.contact_container {
     grid-template-columns: 1fr;
     grid-gap: 2rem;
     gap: 2rem;
   }
  }
  
  @media screen and (max-width: 600px){
   .container.contact_container {
      width: var(--container-width-sm);
   }
  }
  
  
header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
  }
  
  .header_container {
    text-align: center;
    height: 100%;
    position: relative;
  }
  
  .cta {
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
  }
  
  .header_socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
  }
  
  .me {
    background: linear-gradient(var(--color-primary),transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
  }
  
  .scroll_down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 200;
    font-size: 0.9rem;
  }
  
  .cartoon {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-70%, -50%);
            transform: translate(-70%, -50%);
    width: 80vmin;
    height: 80vmin;
  }
  
  
  .cartoon div {
    position: absolute;
    box-sizing: border-box;
  }
  
  .b {
    border: 0.5vmin solid black;
  }
  
  .r {
    border-radius: 100%;
  }
  
  .hb::before
   {
    content: "";
    display: block;
    position: absolute;
  }
  
  .bubble {
    width: 30%;
    height: 20%;
    left: 73%;
    top: 28%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 2vmin;
    background: linear-gradient(var(--color-primary),transparent);
    box-shadow: 0 -0.25vmin, 0 0.125vmin;
    font-family: "Comic Sans", "Comic Neue", sans-serif;
  }
  
  .bubble::before {
    width: 40%;
    height: 100%;
    bottom: -51%;
    border-radius: 50%;
    left: 10%;
    box-shadow: 0.5vmin 0, 2vmin -0.5vmin #6c572c, 2vmin -0.5vmin 0 0.5vmin;
    -webkit-clip-path: polygon(0% 49%, 150% 48%, 150% 100%, 0% 100%);
            clip-path: polygon(0% 49%, 150% 48%, 150% 100%, 0% 100%);
  }
  
  
  @media screen and (max-width: 1024px){
    header {
      height: 70vh;
    }
  }
  
  @media screen and (max-width: 600px){
    header {
      height: 100vh;
    }
    .header_socials, .scroll_down {
      display: none;
    }
  }
.project_container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
  }
  
  .project_item {
    background: var(--color-bg-variant);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
  }
  
  .project_item:hover {
    border-color: var(--color-primary-variant);
    background: transparent;
  }
  
  .project_item-image {
    border-radius: 0.5rem;
    overflow: hidden;
  }
  
  .project_item h3 {
    margin: 1.2rem 0 2rem;
  }
  
  .project-item-cta {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
  }
  
  @media screen and (max-width: 1024px){
   .project_container {
     grid-template-columns: 1fr 1fr;
     grid-gap: 1.2rem;
     gap: 1.2rem;
   }
  }
  
  @media screen and (max-width: 600px){
    .project_container {
      grid-template-columns: 1fr;
      grid-gap: 1rem;
      gap: 1rem;
    }
  }
  
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #000000;
  --color-bg-variant: #000552;
  --color-primary:#81d1ff;
  --color-primary-variant: rgba(0, 0, 0, 0.4);
  --color-white: #9900ff;
  --color-light: rgba(255,255,255,0.6);
  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
  --transition: all 1000ms ease;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: 'Poppins', sans-serif;
  background:  #000000;
  background:  var(--color-bg);
  color: #9900ff;
  color: var(--color-white);
  line-height: 1.7;
}

/* ====== General Style=======*/
.container {
  width:75%;
  width:var(--container-width-lg);
  margin: 0 auto;
}

h1,h2,h3,h4, h5 {
  font-weight: 500;
}

h1{
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
}

section >h2, section > h5 {
  text-align: center;
  color: rgba(255,255,255,0.6);
  color: var(--color-light);
}

section > h2 {
  color: #81d1ff;
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: rgba(255,255,255,0.6);
  color: var(--color-light);
}

a {
  color: #81d1ff;
  color: var(--color-primary);
  transition: all 1000ms ease;
  transition: var(--transition);
}

a:hover {
  color: #9900ff;
  color: var(--color-white);
}

.btn {
  width: -webkit-max-content;
  width: max-content;
  display:inline-block;
  color: #81d1ff;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid #81d1ff;
  border: 1px solid var(--color-primary);
  transition: all 1000ms ease;
  transition: var(--transition);
}

.btn:hover {
  background:#9900ff;
  background:var(--color-white);
  color: #000000;
  color: var(--color-bg);
  border-color: #000000;
  border-color: var(--color-bg);
}

.btn-primary {
  background: #81d1ff;
  background: var(--color-primary);
  color: #000000;
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/*===== Media Query(Tablet)===*/
@media screen and (max-width: 1024px){
  .container {
    width: 86%;
    width: var(--container-width-md);
  }

  section {
    margin-top: 6rem;
  }
}

/*===== Media Query(Mobile)===*/
@media screen and (max-width: 600px){
  .container {
    width: 90%;
    width: var(--container-width-sm);
  }

  section > h2 {
    margin-bottom: 2rem;
  }
}
